@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&display=swap);
html{scroll-behavior:smooth}body{margin:0;padding:0;font-family:"Space Grotesk",sans-serif;background:radial-gradient(#a1a1a1, white);color:#000;width:100vw;min-height:100vh}::selection{background-color:#08131b;color:#fff}.divider{height:2px;margin-left:5px;width:40px;margin-top:10px;background:linear-gradient(45deg, #08131b, #fff)}ul.header{padding:20px;align-items:center;justify-content:center;margin:0;background-color:#03061f;color:#fff;list-style:none;display:flex}ul.header li{padding-left:20px;padding-right:20px;cursor:pointer;transition:all 0.5s ease;font-size:larger}ul.header li:hover{color:#08131b}@media only screen and (max-width: 420px){ul.header{flex-direction:row}}.hero{padding-left:20px;padding-bottom:0;margin-bottom:0}.hero h2{font-size:40px;margin-bottom:0;padding-bottom:0}.hero h1{font-size:45px;margin:0}.hero .hero-title{margin-top:50px}.section-title{font-size:40px;margin:0;margin-left:20px;padding:20px}section{align-items:center;justify-content:center}.cards{display:grid;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr))}@media only screen and (max-width: 420px){.cards{margin-left:25vw}}.cards-cert{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))}@media only screen and (max-width: 600px){.cards-cert{margin-left:20vw}}.cards-cert>button{width:200px !important;height:100px !important;font-size:large}.card{background-color:#ebebeb;width:200px;border-radius:10px;margin:5px;transition:all 0.5s ease;height:110px;text-align:center;justify-content:center;align-items:center}.card:hover{box-shadow:4px 2px 4px 2px;background-color:#f2f2f2}.card .card-title{padding-top:5%;font-size:20px !important}.card .card-icon{font-size:30px}@media only screen and (max-width: 420px){.card{margin-left:-20px}}.cert-card{display:flex;flex-direction:column;width:250px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0;padding:2px;align-items:center;border-radius:5px;margin-bottom:10px;background-color:#f2f2f2;transition:all 0.5s ease;margin-left:3px}.cert-card p{text-align:center}.cert-card:hover{background-color:#ededed}.cert-card img{align-items:center;justify-content:center;margin-left:10%}@media only screen and (max-width: 420px){.cert-card{margin-left:-20px}}button{padding:10px;margin:10px;background-color:#d48a8a;border:0;border-radius:5px;transition:all 0.2s ease;font-family:"Space Grotesk",sans-serif}button:hover{cursor:pointer;background-color:#864949}a{position:relative;color:white;text-decoration:none;transition:all 0.5s ease}a::before{content:'';position:absolute;width:100%;height:2px;bottom:0;left:0;background-color:#fff;visibility:hidden;-webkit-transform:scaleX(0);transform:scaleX(0);transition:all 0.3s ease-in-out}a:hover{-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}a:hover::before{visibility:visible;-webkit-transform:scaleX(1);transform:scaleX(1)}.footer{min-height:80px;border-top-left-radius:20px;border-top-right-radius:20px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;justify-content:center;background-color:#000313;text-align:center;display:flex;color:#fff;padding:20px}.footer .name{font-size:15px}.footer .links{font-size:25px}.footer .links .copy{font-size:15px}.footer>div{margin:5px}@media only screen and (max-width: 420px){.footer{flex-direction:column}}.about{font-family:'Josefin Sans', sans-serif}.about .about-info{margin:10px;line-height:50px;font-size:larger}

